@import url('https://fonts.googleapis.com/css2?family=Lustria&family=Raleway:wght@300;400&family=Work+Sans:wght@300;400;600&display=swap');

/* 
nav: raleway 300
body: work sans 300 400 600
*/

html {
	width: 100vw;
}

body {
	font-family: 'work sans', 'trebuchet ms', -apple-system, BlinkMacSystemFont,
		verdana, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	overflow-x: hidden;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

* {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
}

ul {
	list-style: none;
	padding: 0;
	margin: 0;
}

#App {
	margin: 0;
	width: 100%;
}

/* ======== NAVBAR ========= */

#Navbar {
	display: flex;
	justify-content: center;
	border-bottom: 2px solid lightgray;
}

#Navbar a {
	padding: 0.8rem 3rem;
	text-decoration: none;
	color: black;
	font-size: 1.4rem;
	transition: color 0.5s ease;
	font-family: 'raleway', -apple-system, BlinkMacSystemFont, verdana,
		'trebuchet ms', sans-serif;
	font-weight: 300;
}

/* ======== BODY ========== */

#body {
	max-width: 1000px;
	margin: 0 auto;
}

/* ======== ABOUT ========= */

#About {
	padding: 0 3rem;
	text-align: center;
	line-height: 1.4;
}

#About h1 {
	font-size: 3rem;
	margin: 3rem 0;
	color: navy;
	line-height: 1.2;
}

#About h1 span {
	white-space: nowrap;
}

#About p {
	font-size: 2rem;
	margin-bottom: 1.4rem;
}

#About p:nth-child(4) {
	padding: 1rem 0;
	margin: 1rem 0;
	font-size: 2.2rem;
}

#About p:nth-child(6) {
	font-size: 1.6rem;
}

#About a {
	color: navy;
}

/* ======== PROJECTS ======== */

#Projects {
	margin: 0 auto;
}

#Projects li {
	text-align: center;
	margin: 2rem 0 4rem;
}

/* PROJECT CARD */

.ProjectCard {
	margin: 4rem 0 7rem 0;
}

.ProjectCard .image-container {
	width: 70%;
	line-height: 2;
	margin: 0 auto;
}

.ProjectCard img {
	width: 100%;
	border-radius: 0.8rem;
	box-shadow: 0 0 30px -20px black;
	border-bottom: 5px solid slateblue;
}

.ProjectCard h1 {
	margin: 1rem 0;
}

.ProjectCard .links-container {
	display: flex;
	justify-content: center;
}

.ProjectCard a {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	color: navy;
	border: 1px solid navy;
	border-right: none;
	border-left: none;
	text-decoration: none;
	padding: 0.2rem;
	margin: 0 1rem;
	font-size: 1.6rem;
}

.ProjectCard span {
	margin: 0;
}

.ProjectCard a:visited {
	color: navy;
	padding: 0 1rem;
	font-size: 1.6rem;
}

.ProjectCard svg {
	height: 1.4rem;
}

/* ======== STACK ======== */

#Stack {
	padding: 0 3rem;
	text-align: center;
	line-height: 1.4;
}

#Stack h1 {
	font-size: 3rem;
	margin: 3rem 0;
	color: navy;
	line-height: 1.2;
}

#Stack h1 span {
	color: orchid;
}

#Stack img {
	height: 4rem;
	margin: 0 0.8rem;
}

.separator {
	width: 70%;
	margin: 2rem auto;
	height: 0.2rem;
	background: lavenderblush;
}
/* ======== FADE EFFECTS ============ */

/* PAGE FADES */

.fadeIn {
	animation: 0.25s fadeIn forwards;
}

.fadeOut {
	animation: 0.25s fadeOut forwards;
}

@keyframes fadeIn {
	from {
		opacity: 0;
		transform: translate(-20px, 0);
	}
	to {
		opacity: 1;
		transform: translate(0px, 0px);
	}
}

@keyframes fadeOut {
	from {
		opacity: 1;
		transform: translate(0px, 0px);
	}
	to {
		transform: translate(-20px, 0);
		opacity: 0;
	}
}

@media all and (max-width: 560px) {
	:root {
		font-size: 10px;
	}
	#App {
		margin: 0;
		width: 100%;
	}

	#Navbar a {
		padding: 0.8rem 2rem;
	}
	.ProjectCard .links-container {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.ProjectCard a {
		border-top: none;
		width: fit-content;
		margin: 0.4rem 2rem;
		font-size: 1.6rem;
	}

	#Stack h1 {
		font-size: 2rem;
	}

	#About {
		padding: 1rem;
	}

	#About h1 {
		font-size: 3rem;
		margin: 2rem 0;
	}

	#About p {
		margin: 0;
		padding: 0 1rem !important;
	}

	#About .separator {
		margin: 1rem auto;
	}
}
